import React from "react";

function RSCardDeck() {
  return (
    <main>
      <div className="work-wrapper">
        <section className="section-wrapper first-section">
          <h1>RuneScape Card Deck</h1>
          <div className="project-image-wrapper">
            <div className="single-image-wrapper">
              <img
                loading="lazy"
                src="../projects/runescape-card-deck/rs3-deck-header.png"
              />
            </div>
          </div>
          <div className="project-info grid-x">
            <div className="project-info-list cell small-12 large-3">
              <ul>
                <li>
                  <span className="project-info-list-subtitle">Category:</span>{" "}
                  Web Design, Illustration
                </li>
                <li>
                  <span className="project-info-list-subtitle">Team Size:</span>{" "}
                  1
                </li>
                <li>
                  <span className="project-info-list-subtitle">Year:</span> 2020
                </li>
              </ul>
            </div>
            <div className="project-description cell small-12 large-9">
              <p>
                The goal with this project was to combine my love for
                interesting playing card designs and a game that has been with
                me through most of my life, RuneScape.
              </p>
              <p>
                I wanted to make a black, clean looking deck of cards based on
                RuneScape as a personal project seeing as it is something I
                could use on the many game nights I have with my friends.
              </p>
              <p>
                I, then, made a website to display the entire deck as a way of
                sharing it, available on:
                <br />
                <a
                  href="https://diogofreitascastro.github.io/RS3-Card-Deck/"
                  style={{ fontStyle: "italic", fontWeight: "bold" }}
                >
                  https://diogofreitascastro.github.io/RS3-Card-Deck/
                </a>
              </p>
            </div>
          </div>
        </section>
        {/* PROCESS */}
        <section className="section-wrapper">
          <h1>Cards</h1>
          <div className="grid">
            <div className="text-wrapper">
              <p>
                After deciding I wanted the card deck to be RuneScape themed and
                have a sleek design, I had to find characters from the game that
                would play the King, Queen and Duke, as well as what I could use
                for the Aces.
              </p>
              <p>
                Having decided these, I started looking into how well I could
                portray the card while having in mind the 180º rotation that the
                figures have.
              </p>
              <p>
                Then I started by sketching out on Illustrator what the profiled
                shape could be, leaving room for the 180º rotation, upgrading
                the brushed sketch into pen-tool shapes that would then be
                filled with gradients.
              </p>
            </div>
            <div className="image-and-caption">
              <div className="image-grid grid">
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/runescape-card-deck/Saradomin.png"
                    alt="Saradomin, the God."
                  />
                </div>
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/runescape-card-deck/Saradomin-sketch.png"
                    alt="Saradomin's first sketch"
                  />
                </div>
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/runescape-card-deck/Saradomin-gradient.png"
                    alt="Saradomin's final design"
                  />
                </div>
              </div>
              <div className="caption">
                <p>
                  Saradomin, the character picked to depict the King, the sketch
                  and the final design.
                </p>
              </div>
            </div>
            <div className="text-wrapper">
              <p>
                After having the main element of the cards drawn, I looked for
                other elements I could use for the borders of the card, and I
                found these geometric swirls on their website.
              </p>
            </div>
            <div className="image-and-caption">
              <div className="image-grid grid">
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/runescape-card-deck/RuneScape-website-swirls.png"
                    alt="Geometric swirls"
                  />
                </div>
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/runescape-card-deck/10-spades-nobg.png"
                    alt="10 of spades with the geometric swirls on the corners"
                  />
                </div>
              </div>
              <div className="caption">
                <p>
                  The geometric swirls on the website and the 10 of spades with
                  the swirls on the corners.
                </p>
              </div>
            </div>
            <div className="text-wrapper">
              <p>
                For the back of the card, I wanted something elaborate and very
                detailed while still keeping some sort of symmetry, something
                that would look good even when flipped. I found this boss called
                "Seiryu" that's basically a big dragon-snake that I could shape
                into an S. Then for the background I decided to mess around with
                circles and spikes.
              </p>
            </div>
            <div className="image-and-caption">
              <div className="image-grid grid mobile-column">
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/runescape-card-deck/Seiryu-wireframe-3d model.jpg"
                    alt="Seiryu's model in-game"
                  />
                </div>
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/runescape-card-deck/Spirit-dragon-pet.png"
                    alt="creature simillar to seiryu to see his potential full body"
                  />
                </div>
              </div>
              <div className="caption">
                <p>
                  Seiryu seen during the boss fight in-game and another monster
                  that's similar looking, to see what Seiryu's full body would
                  potentially look like.
                </p>
              </div>
            </div>
            <div className="image-and-caption">
              <div className="image-grid grid">
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/runescape-card-deck/card-back-sketch.png"
                    alt="sketch for the card back"
                  />
                </div>
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/runescape-card-deck/card-back-seiryu.png"
                    alt="Seiryu in an S design"
                  />
                </div>
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/runescape-card-deck/card-back-background.png"
                    alt="background for the card back"
                  />
                </div>
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/runescape-card-deck/card-back-final.png"
                    alt="final card back design"
                  />
                </div>
              </div>
              <div className="caption">
                <p>
                  The sketch, Seiryu for the main element, the background and
                  the final design for the card back.
                </p>
              </div>
            </div>
          </div>
        </section>
        <section className="section-wrapper">
          <h1>Website</h1>
          <div className="grid">
            <div className="text-wrapper">
              <p>
                The website idea started as a way to present the cards to
                everyone. Since I enjoy webdesign I thought I'd try and make a
                little website for them, as I also wanted to try some new CSS/JS
                stuff. The full website can be seen and interacted with on:
                <br />
                <a
                  href="https://diogofreitascastro.github.io/RS3-Card-Deck/"
                  style={{ fontStyle: "italic", fontWeight: "bold" }}
                >
                  https://diogofreitascastro.github.io/RS3-Card-Deck/
                </a>
              </p>
            </div>
            <div className="image-and-caption">
              <div className="image-grid grid">
                <div className="single-image-wrapper">
                  <video controls muted loop>
                    <source
                      src="../projects/runescape-card-deck/playing-cards-website-preview.mp4"
                      type="video/mp4"
                    />
                  </video>
                </div>
              </div>
              <div className="caption">
                <p>Preview of the website with the 3D Aces as buttons.</p>
              </div>
            </div>
          </div>
        </section>
      </div>
      {/* IMAGE GALLERY */}
      <section className="section-wrapper">
        <h1>Gallery</h1>
        <div className="masonry">
          <div className="single-image-wrapper">
            <img
              loading="lazy"
              src="../projects/runescape-card-deck/rs-cards-mockup-1.png"
              alt="placeholder"
            />
          </div>
          <div className="single-image-wrapper">
            <img
              loading="lazy"
              src="../projects/runescape-card-deck/rs-cards-mockup-1.1.png"
              alt="placeholder"
            />
          </div>
          <div className="single-image-wrapper">
            <img
              loading="lazy"
              src="../projects/runescape-card-deck/rs-cards-website-landing.png"
              alt="placeholder"
            />
          </div>
        </div>
      </section>
    </main>
  );
}

export default RSCardDeck;
