import React from "react";

function SuityBot() {
  return (
    <main>
      <div className="work-wrapper">
        <section className="section-wrapper first-section">
          <h1>Suity Bot</h1>
          <div className="project-image-wrapper">
            <div className="single-image-wrapper">
              <img
                loading="lazy"
                src="../projects/suity-bot/suity-bot-header.png"
              />
            </div>
          </div>
          <div className="project-info grid-x">
            <div className="project-info-list cell small-12 large-3">
              <ul>
                <li>
                  <span className="project-info-list-subtitle">Category:</span>{" "}
                  Graphic Design
                </li>
                <li>
                  <span className="project-info-list-subtitle">Team Size:</span>{" "}
                  1
                </li>
                <li>
                  <span className="project-info-list-subtitle">Year:</span> 2022
                </li>
              </ul>
            </div>
            <div className="project-description cell small-12 large-9">
              <p>
                Suity Bot is a Discord bot used by many RuneScape players. The
                main purpose of Suity Bot is to allow players to view recent
                trade prices of in-game items; These prices are submitted by
                players, for players. The creators of Suity Bot came to me in
                search of a re-branding of the Suity bot and Discord server.
              </p>
            </div>
          </div>
        </section>
        {/* PROCESS */}
        <section className="section-wrapper">
          <h1>The Logo</h1>
          <div className="grid">
            <div className="text-wrapper">
              <p>
                Suity bot's old Discord server's name and logo was "PCT",
                standing for "Price Check &amp; Trade", and the bot had a
                picture of a figure in a suit. The requirements I was given was
                that the logo must be simplified and rounded, to match the shape
                and size the owners intended for Discord.
              </p>
              <p>
                To start I decided that the logo should be hexagonal. This would
                allow it to stand out from most other server icons which are
                typically circular. Where most server icons ignore background
                transparency, I could use the negative space to make the logo
                stand out further.
              </p>
              <p>
                I included a tie as an element that was present in the old brand
                in the negative space of the "U" in "Suity", and an ON button
                for the "O" in "Bot" or a mechanical cog to give the idea of
                bot. The green and red arrows are there to remit to the prices
                going up and down. All of this is explained in the images below.
              </p>
            </div>
            <div className="image-and-caption">
              <div className="image-grid grid">
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/suity-bot/logo-elements-1.png"
                    alt="The elements that compose the logo"
                  />
                </div>
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/suity-bot/logo-elements-2.png"
                    alt="The elements that compose the logo"
                  />
                </div>
              </div>
              <div className="caption">
                <p>The different elements that compose the logo.</p>
              </div>
              <div className="image-grid grid">
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/suity-bot/Suity_Bot_first-pass.png"
                    alt="First feedback with the 2 versions of the logo"
                  />
                </div>
              </div>
              <div className="caption">
                <p>First client feedback presentation page.</p>
              </div>
            </div>
            <div className="text-wrapper">
              <p>
                After choosing the first option - with the ON symbol - in the
                image above, the client wanted to experiment with the sizing of
                different elements in the icon. There wasn't much room for
                expansion for something that would mainly be displayed at
                50x50px but I decided to bring the outline closer to the edge,
                allowing for more space of the other icon elements. After
                discussing the options displayed below, the client was happy to
                continue with option 4.
              </p>
            </div>
            <div className="image-and-caption">
              <div className="image-grid grid">
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/suity-bot/suity-bot-sizing-options.png"
                    alt="Different options with different sizes of the elements inside"
                  />
                </div>
              </div>
              <div className="caption">
                <p>The different options in regards to element sizing.</p>
              </div>
            </div>
            <div className="text-wrapper">
              <p>
                Now that a logo was chosen, the clients let me know of all the
                components they required, which were as follows:
              </p>
              <ol>
                <li>1 - Static base image</li>
                <li>2 - Animated base image</li>
                <li>3 - Banner for Patreon</li>
                <li>4 - Banner for Discord</li>
                <li>5 - "Dyed" variants of the logo</li>
              </ol>
            </div>
          </div>
        </section>
        <section className="section-wrapper">
          <h1>Animating the logo</h1>
          <div className="grid">
            <div className="text-wrapper">
              <p>
                After the requirements were set out on the static logo variants,
                I moved on to animated variants.
              </p>
              <p>
                While creating the logo, I played with an idea of having numbers
                moving up and down in the middle to mimic animations seen on
                Crypto sites. However, this wasn't ideal for the client as they
                wanted something that would suit both their own Discord server
                and the bot. As a result, I made a simpler animation that would
                suit both platforms instead of being tied to a specific function
                of the bot.
              </p>
            </div>
            <div className="image-and-caption">
              <div className="image-grid grid">
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/suity-bot/suity-bot-animation-stonks.gif"
                    alt="First animation"
                  />
                </div>
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/suity-bot/suity-bot-animation-2.gif"
                    alt="Second animation"
                  />
                </div>
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/suity-bot/suity-bot-animation-3.gif"
                    alt="Third animation"
                  />
                </div>
              </div>
              <div className="caption">
                <p>The first three iterations of the animated logo.</p>
              </div>
              <div className="image-grid grid">
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/suity-bot/suity-bot-final-animation.gif"
                    alt="Final animation"
                  />
                </div>
              </div>
              <div className="caption">
                <p>The final animated version, currently in use.</p>
              </div>
            </div>
          </div>
        </section>
        <section className="section-wrapper">
          <h1>The Banners</h1>
          <div className="grid">
            <div className="text-wrapper">
              <p>
                When it came to the banners, I presented the client with the
                following two images, one for their Discord server and one for
                their Patreon site and the client was pretty happy with them. I
                later made a third image which was not requested by the client,
                this one is displayed on the web when an invite for the Discord
                server is received. This can be seen below the banners.
              </p>
            </div>
            <div className="image-and-caption">
              <div className="image-grid grid">
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/suity-bot/suity-bot-discord-banner.png"
                    alt="Discord banner"
                  />
                </div>
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/suity-bot/suity-bot-patreon-banner.png"
                    alt="Patreon banner"
                  />
                </div>
              </div>
              <div className="caption">
                <p>The banners for Discord and Patreon.</p>
              </div>
              <div className="image-grid grid">
                <img
                  loading="lazy"
                  src="../projects/suity-bot/suity-bot-discord-invite-1080p.png"
                  alt="Discord web invite"
                />
              </div>
              <div className="caption">
                <p>The background for the Discord invite screen.</p>
              </div>
            </div>
          </div>
        </section>
        <section className="section-wrapper">
          <h1>The Patreon Badges</h1>
          <div className="grid">
            <div className="text-wrapper">
              <p>
                Lastly, for the "dyed" variants of the logo, I had to animate
                them in a way that reflected how dyes appear in RuneScape for
                "Barrows", "Shadow", "Ice", "Third-age", and "Blood", and was
                then asked to make a gold one and one inspired by japanese
                cherry blossoms. The results were the following:
              </p>
            </div>
            <div className="image-and-caption">
              <div className="image-grid grid">
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/suity-bot/Suity-Barrows.gif"
                    alt="Barrows dyed logo"
                  />
                </div>
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/suity-bot/Suity-Shadow.gif"
                    alt="Shadow dyed logo"
                  />
                </div>
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/suity-bot/Suity-Ice.gif"
                    alt="Ices dyed logo"
                  />
                </div>
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/suity-bot/Suity-Blood.gif"
                    alt="Blood dyed logo"
                  />
                </div>
              </div>
              <div className="image-grid grid">
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/suity-bot/Suity-Third-age.gif"
                    alt="Third-age dyed logo"
                  />
                </div>
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/suity-bot/Suity-Gold.gif"
                    alt="Gold logo"
                  />
                </div>
                <div className="single-image-wrapper">
                  <img
                    loading="lazy"
                    src="../projects/suity-bot/Suity-Blossom.gif"
                    alt="Sakura Blossoms logo"
                  />
                </div>
              </div>
              <div className="caption">
                <p>The banners for Discord and Patreon.</p>
              </div>
            </div>
          </div>
        </section>
        {/* IMAGE GALLERY */}
        {/*
				<section class="section-wrapper">
					<h1>Gallery</h1>
					<div class="masonry">
						<div class="single-image-wrapper">
							<img loading="lazy" src="" alt="placeholder">
						</div>
						<div class="single-image-wrapper">
							<img loading="lazy" src="" alt="placeholder">
						</div>
						<div class="single-image-wrapper">
							<img loading="lazy" src="" alt="placeholder">
						</div>
					</div>
				</section>
			*/}
      </div>
    </main>
  );
}

export default SuityBot;
