import React from "react";

function ProjectItem(props) {
  return (
    <div className={`project-grid-card ${props.category}`}>
      <h2>{props.title}</h2>
      <a href={props.link} title={props.title}>
        <div className="project-image-holder">
          <img loading="lazy" src={props.img} />
        </div>
      </a>
    </div>
  );
}

export default ProjectItem;
